<template>
  <div v-if="videoList && communicate">
    <van-sticky>
      <div class="user" style="background: #fff">
        <div class="flex" v-for="(item, index) in videoList" :key="index">
          <img :src="item.author_img" alt="" />
          <div class="nickname">
            {{ item.author_nickname }}
            <div class="day">{{ item.create_time }}</div>
          </div>
          <div class="btn">+ 关注</div>
        </div>
      </div>
    </van-sticky>
    <div class="content" v-for="(item, index) in videoList" :key="index">
      {{ item.moment_title }}
      <div>
        <video :src="item.clip_advanced_url" controls></video>
      </div>
      <div class="goodjob">
        <van-icon name="good-job-o" @click="goodJob" /> {{ item.praise_num }}
      </div>
    </div>
    <div class="comment">
      <p class="title">评论</p>
      <div class="user" v-for="(item, index) in communicate" :key="index">
        <div
          class="flex"
          style="width: 100%; justify-content: space-between; padding: 0.4rem"
        >
          <img :src="item.img" alt="" />
          <div class="nickname">
            <div>
              {{ item.nickname }}
              <div class="day">{{ item.create_time }}</div>
            </div>
            <div>{{ item.comment }}</div>
          </div>
          <van-icon name="good-job-o" @click="goodJob" />
        </div>
      </div>
    </div>
    <van-goods-action>
      <van-goods-action-icon icon="records-o" text="写评论" @click="goodJob" />
      <van-goods-action-button
        type="danger"
        :text="`￥${videoList[0].price} ${videoList[0].button_title}`"
        @click="onClickButton"
      />
    </van-goods-action>
  </div>
</template>

<script>
import { Toast } from "vant";

export default {
  data() {
    return {
      videoList: null,
      communicate: null,
    };
  },
  methods: {
    getVideoList() {
      this.axios
        .get(
          `moment/get_moment_by_idlist?moment_id_list=${this.$route.query.id}`
        )
        .then((res) => {
          console.log(res.data.data?.moment_list);
          this.videoList = res.data.data?.moment_list;
        });
    },
    getCommunicateist() {
      this.axios
        .get(
          `communicate/mizone_comment_list?comment_type=moment&moment_id=${this.$route.query.id}&page_size=10&page_index=1`
        )
        .then((res) => {
          console.log(res.data.data.comment_list);
          this.communicate = res.data.data.comment_list;
        });
    },
    goodJob() {
      Toast("请前往app参与");
    },
    onClickButton(){
      Toast("请前往app参与");
    }
  },
  created() {
    this.getVideoList();
    this.getCommunicateist();
  },
};
</script>

<style lang="less" scoped>
.user {
  width: 100%;
  margin-bottom: 0.2rem;
  display: flex;
  align-items: center;
  padding-top: 0.3rem;
  .flex {
    display: flex;
    margin: auto;
    align-items: center;
  }
  img {
    margin-right: 0.16rem;
    width: 0.733rem;
    height: 0.733rem;
    border-radius: 0.52rem;
    overflow: hidden;
  }
  .nickname {
    font-size: 0.28rem;
    font-weight: 500;
    color: #161616;
    line-height: 1.4;
    width: 64vw;
  }
  .day {
    font-size: 0.2rem;
    font-weight: 500;
    color: #757575;
  }
  .btn {
    padding: 0 0.33rem;
    height: 0.48rem;
    line-height: 0.48rem;
    border-radius: 0.32rem;
    border: 1px solid #ff5528;
    font-size: 0.24rem;
    font-weight: 500;
    text-align: center;
    color: #fff;
    background: #ff5528;
  }
}
.content {
  padding: 0 0.24rem;
  overflow: hidden;
  margin-top: 0.18rem;
  font-size: 0.3rem;
  color: #333;
  line-height: 1.8;
  word-wrap: break-word;
  video {
    margin-top: 0.2rem;
    border-radius: 0.14rem;
    width: 100%;
    height: 196.875px;
    object-fit: cover;
    border-radius: 10px;
  }
  .goodjob {
    text-align: right;
    line-height: 0.32rem;
    font-size: 0.24rem;
    color: rgba(0, 0, 0, 0.54);
  }
}
.comment {
  width: 100%;
  border-top: 0.5px solid rgba(0, 0, 0, 0.15);
  padding: 0.32rem 0;

  .title {
    text-align: left;
    height: 0.48rem;
    font-size: 0.32rem;
    font-weight: 700;
    color: rgba(0, 0, 0, 0.87);
    line-height: 0.48rem;
    margin-bottom: 0.32rem;
    padding-left: 0.24rem;
  }
}
</style>